'use client'
import React,{ useState } from "react"
import { Arrow } from "../icon/arrow";


export default function PromptBox() {


    const [prompt, setPrompt] = useState('');
    return <form className={`w-full ${false ? "max-w-3xl":" max-w-2xl"} bg-white/10   backdrop-blur-md drop-shadow-lg border-white border-2  rounded-3xl mt-4 transition-all shadow-2xs p-4 `}> 
        <textarea
        className="outline-none overflow-hidden resize-none bg-transparent break-words w-full"
        rows={2}
        placeholder="Type here your fav lyrics"
        onChange={(e) => setPrompt(e.target.value)}
        value={prompt}
        
        /> 
        <div className={`${prompt? "bg-purple-200 text-purple-800": "bg-purple-800 text-white"} flex ml-[590px] justify-end   place-content-end p-2 rounded-full `}> 
            <Arrow />
        </div>

    </form>
}